.contact {
    width: 100%;
    height: 100vh;
    background-color: black;
    overflow: hidden;
    border: 7px solid rgb(77, 140, 157);
    font-family: 'Niramit', sans-serif;
}

.contact h1 {
    color: rgb(219, 97, 15);
    font-size: 25px;
    text-align: center;

}

form {
    margin: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

form label {
    color: #fff;
    margin: .5rem;
}

form input {
    width: 40%;
    padding: 20px;   
}

form textarea {
    width:40% ;
    padding: 20px;
}
form button {
    padding: 1rem 4rem;
    margin-top: 1rem;
    background-color:rgb(77, 140, 157) ;
    color: #fff;
    border: 2px solid #fff;
    cursor: pointer;
}


